    margin-bottom:.5em;
    margin-top:.5em
}
#feature ul {
    margin-left:2em
}
footer p {
    text-align:right;
    font-size:85%;
    border-top:1px dashed #999
}
article, nav h1+ul, nav section li.nav-3 ul {
    counter-reset:chapter
}
#toc {
    counter-reset:h2
}
#toc li.toc-h2 {
    counter-reset:h3
}
#toc li.toc-h2 a:before {
    counter-increment:h2;
    content:counter(h2)". "
}
#toc li.toc-h2~li.toc-h3 a:before {
    counter-increment:h3;
    content:counter(h2)"."counter(h3)"\3000"
}
#toc li.toc-h3 + li.toc-h4 {
  counter-reset:h4
}
#toc li.toc-h4 a:before {
  counter-increment:h4;
  content:counter(h2)"."counter(h3)"."counter(h4)"\3000"
}
article div.chapter{
    counter-reset:section
}
article.bookIndex div.chapter+ul ul {
    counter-reset:ul;
}
article.bookIndex ul {
    list-style:none
}
article div.chapter h2:before, nav h1+ul li a:before, nav section li.nav-3 ul li a:before {
    counter-increment:chapter;
    content:counter(chapter)". "
}
article div.chapter~h3:before {
    counter-increment:section;
    content:counter(chapter)"." counter(section)"\3000"
}
article div.twelve h3 {
  counter-reset:h5;
}
article div.twelve h3 ~ h4:before {
  counter-increment: h5;
  content:counter(chapter)"." counter(section)"."counter(h5)"\3000"
}
article div.twelve h4{
  counter-reset:h6;
}
article div.twelve h4 ~ h5:before {
  counter-increment: h6;
  content:counter(chapter)"." counter(section)"."counter(h5)"."counter(h6)"\3000"
}
article.bookIndex div.chapter+ul li a:before, article.bookIndex div.chapter+ul ul li a:before {
    counter-increment: ul;
    content:counters(ul, ".") " ";
}
article ul.reference-list {
    counter-reset:li;
    list-style:none;
    font-family:Consolas, Monaco, 'Andale Mono', monospace;
    margin-left:0
}
article ul.reference-list li:before {
    counter-increment:li;
    content:"["counter(li)"] "
}
